/* Visibilità Password */
.btn-eye {
  background-position: center !important;
  background-repeat: no-repeat !important;
  width: 28px;
  height: 22px;
  position: absolute;
  right: .5rem;
  top: .7rem;
  z-index: 10;
  color: $gray-secondary;
  cursor: pointer;
}
