/* Password Strength Meter */
.psw-wrapper  {
  padding: 0 .75rem;
}
.psw-meter  {
  height: 3px;
  max-width: 180px;
}
.psw-meter .col-3 {
  height: 3px;
}
.psw-percent,
.psw-text {
  font-size: 0.75rem;
  display: block;
  padding: .25rem 0;
  font-weight: 500;
}
.psw-percent {
  margin-right: 5px;
}
